/* Web Fonts ----------------------- */
@font-face {
  font-family: "Abolition Regular";
  src: url("../fonts/abolition-regular-webfont.woff") format("woff");
}

/* Base Styles --------------------- */
* {
  box-sizing: border-box;
}
html {
  font-size: 1.25em; /* 20px on most screens */
}
body {
  margin: 1rem;
  color: #454959;
  font: 300 100%/1.5 "Work Sans", sans-serif;
}
h1,
h2 {
  font-family: "Abolition Regular", Impact, Haettenschweiler,
    "Arial Narrow Bold", sans-serif;
  font-weight: normal;
  line-height: 1.1;
}
a {
  color: #cc4e13;
  text-decoration: none;
}
h1 {
  font-size: 4rem;
  text-shadow: 1px 2px 3px rgba(0, 10, 30, 0.5);
}
h1::after {
  content: "\2193";
  display: block;
}

h2 {
  font-size: 2.5rem;
}
h3 {
  font-size: 1.25rem;
}
img {
  width: 100%; /* responsive images */
  border-radius: 10px;
}

/* Main Styles --------------------- */
header {
  padding: 10vh 1rem;
  margin: -1rem -1rem 1rem;
  background: url(../img/mountains.jpg) center / cover no-repeat #f3753b;
  background-blend-mode: multiply;
  color: white;
}

header,
footer {
  text-align: center;
}
.title {
  letter-spacing: 1px;
}
main {
  max-width: 1000px;
  margin: 0 auto;
  hyphens: auto;
}
section {
  padding: 4rem 0;
  border-bottom: 1px dashed #9599a9;
}
#wildlife {
  padding: 20% 15%;
  border-top: 12px solid #f3753b;
  border-radius: 12px 12px 0 0;
  background: url(../img/bear.jpg) center / cover #454959;
  color: white;
  box-shadow: inset 0 0 20px 10px rgba(0, 10, 30, 0.8);
}
#wildlife a {
  color: #ebaa8d;
}

.callout {
  display: block;
  width: 250px;
  padding: 0.25rem 1rem;
  border-radius: 20px/10px;
  box-shadow: 1px 2px 3px rgba(0, 10, 30, 0.8);
  margin: 1rem auto;
  background-image: linear-gradient(
    -180deg,
    #cc4e13 0%,
    rgb(206, 31, 19) 25%,
    rgb(252, 221, 173) 100%
  );
  color: white;
  text-align: center;
  font-size: 1em;
}

.column:first-child {
  margin-bottom: 4rem;
}

/* Media Queries ------------------- */
@media screen and (min-width: 720px) {
  header {
    padding: 20vh 1rem;
  }
  h1 {
    font-size: 5rem;
  }
  h1::after {
    margin-top: 1rem;
  }
  .title {
    font-size: 1.25rem;
  }
  .intro {
    padding: 0 15%;
    font-size: 1.1rem;
  }
  .column {
    display: inline-block;
    width: 48%;
    margin-right: -4px;
  }
  .column:first-child {
    margin-right: calc(4% - 4px);
    margin-bottom: 0;
  }
}
